import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';
import { Push, PushOptions, PushObject } from '@ionic-native/push';
import { RegisterPushTokenRestService } from '../rest/register-push-token-rest-service/register-push-token-rest-service';
import { SettingsProvider } from '../storage/settings/settings';


/**
 *
 */
@Injectable()
export class PushNotificationsHandlerProvider {
  pushObject: PushObject
  constructor(
    public platform: Platform,
    public push: Push,
    protected settings: SettingsProvider,
    protected registerPushService: RegisterPushTokenRestService
  ) {
  }

  init() {
    if (this.platform.is('cordova')) {
      // to check if we have permission
      /*this.push.hasPermission()
        .then((res: any) => {
          if (res.isEnabled) {
            console.log('We have permission to send push notifications');
          } else {
            console.log('We do not have permission to send push notifications');
          }
        });
        */
      // to initialize push notifications
      const options: PushOptions = {
        android: {
          senderID: this.settings.allSettings?this.settings.allSettings.pushSenderId:undefined
        },
        
        ios: {
          alert: 'true',
          badge: true,
          sound: 'false'
        },
        windows: {}
      };

      const pushObject: PushObject = this.push.init(options);

      pushObject.on('registration')
        .subscribe((registration: any) => {
          console.debug('Device registered', registration)
          this.registerPushService.setPushToken(registration.registrationId)
            .subscribe(response => console.debug("Set Push token status: ", response.status));
        });

      pushObject.on('error').subscribe(error => console.error('Error with Push plugin', error));
      this.pushObject = pushObject;
    }
  }

  public getPushObject(): PushObject {
    return this.pushObject;
  }
}



